@charset "UTF-8";
/*  UIAM - 02031998 */

/* Font imports remain in global CSS, not in CSS modules */
/* These should be in your globals.css or layout */

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: var(--font-roboto);
}

.main {
    background-color: var(--color-f3);
}

.textDanger {
    color: red;
    font: var(--font-12);
}

/* CSS Variables remain in :root (global) */
/* These should be in your globals.css */

.container {
    max-width: 1394px;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
}

@media(max-width:1750px) {
    .container {
        max-width: 1274px;
    }
}

@media(max-width:1400px) {
    .container {
        max-width: 1024px;
    }
}

@media(max-width:1366px) {
    .container {
        max-width: 1000px;
    }
}

@media(max-width:1200px) {
    .container {
        max-width: 844px;
    }
}

@media(max-width:1024px) {
    .container {
        max-width: 758;
    }
}

@media(min-width:2500px) {
    .container {
        max-width: 1780px;
        padding-left: 1.5rem;
        padding-right: 1.5rem;
    }
}

.after,
.before {
    box-sizing: border-box;
}

.audio,
.canvas,
.embed,
.iframe,
.img,
.object,
.svg,
.video {
    display: block;
    max-width: 100%;
    width: 100%;
}

.body {
    margin: 0;
    -webkit-text-size-adjust: 100%;
    -webkit-tap-highlight-color: transparent;
    background-color: var(--color-f6);
    scroll-behavior: unset !important;
}

.paragraph {
    font: var(--font-16);
    font-weight: normal;
    font-family: var(--font-roboto);
    color: var(--color-black);
    margin-bottom: 3rem;
}

.paragraph:last-of-type {
    margin-bottom: 0;
}

@media(max-width:1549px) {
    .paragraph {
        margin-bottom: 2rem;
    }
}

.paragraph:empty {
    display: none !important;
}

.anchor {
    text-decoration: none;
    transition: all 0.5s ease;
    -webkit-transition: all 0.5s ease;
    -moz-transition: all 0.5s ease;
    -ms-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
}

.figure {
    margin-bottom: 0;
}

.siteMain {
    position: relative;
}

.heading1 {
    font: var(--font-60);
    color: var(--color-black)
}

.heading2 {
    font: var(--font-52);
    color: var(--color-03);
    font-weight: 600;
    letter-spacing: -0.84px;
}

.heading3 {
    font: var(--font-36);
    color: var(--color-black);
}

.heading4 {
    font: var(--font-24);
    color: var(--color-black);
}

.heading5 {
    font: var(--font-21);
    color: var(--color-black);
}

.heading6 {
    font: var(--font-18);
    color: var(--color-black);
}

/* Webkit scrollbar remains global */
/* These should be in your globals.css */

.overlapBtn {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    font-size: 0;
}

.readBtn {
    display: inline-block;
    border-radius: 50%;
}

.readBtn img {
    width: 2.8rem;
}

.readBtn:hover {
    background-color: var(--color-white);
}

.secTitle h5 {
    font: var(--font-21);
    font-weight: 600;
    color: var(--color-03);
    letter-spacing: 2.1px;
}

.secTitle h2 {
    font-weight: 200;
    color: var(--color-21);
}

.notifyBtn {
    position: absolute;
    bottom: 3rem;
    right: 4rem;
    z-index: 1;
}

.notifyBtn a {
    display: inline-flex;
    align-items: center;
    gap: 3rem;
    font: var(--font-21);
    font-weight: normal;
    letter-spacing: 2.1px;
    text-transform: uppercase;
    color: var(--color-white);
    padding: 1.2rem 2.4rem 1.2rem 1.9rem;
    background: var(--color-f8);
    background: rgba(248, 152, 35, 0.76);
    backdrop-filter: blur(2px);
    -webkit-backdrop-filter: blur(2px);
}

.notifyBtn a img {
    width: 2.9rem;
}

.swiperNav {
    display: flex;
    gap: 0.4rem;
}

/* Swiper styles remain global */
/* These should be in your globals.css */

.scrollbarWrap {
    position: relative;
    display: flex;
    align-self: center;
}

.scrollbarWrap::before {
    width: 100%;
    height: 0.1rem;
    position: absolute;
    content: "";
    background: #BFC7C4;
    top: 0.1rem;
    z-index: 1;
}

.scrollbarWrap .swiperHorizontal .swiperScrollbar,
.faciltySection .swiperScrollbar.swiperScrollbarHorizontal {
    position: static;
}

.scrollbarWrap .swiperScrollbarDrag {
    background: var(--color-bb);
    box-shadow: 0px 3px 6px #00000012;
}

.scrollbarWrap .swiperScrollbar.swiperScrollbarHorizontal {
    left: 0;
    width: 100%;
    height: 0.4rem;
    background-color: transparent;
    bottom: 0;
    top: 0;
}

.shine {
    position: relative;
    overflow: hidden;
}

.shine::before {
    background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.3) 100%);
    content: "";
    display: block;
    height: 100%;
    left: -100%;
    position: absolute;
    top: 0;
    transform: skewX(-25deg);
    width: 50%;
    z-index: 2;
}

.shine:hover::before,
.shine:focus::before {
    -webkit-animation: shine 0.85s;
    animation: shine 0.85s;
}

@-webkit-keyframes shine {
    100% {
        left: 125%;
    }
}

@keyframes shine {
    100% {
        left: 125%;
    }
}

.fullWidth {
    max-width: 1920px;
    margin-inline: auto;
}

.fullTouch {
    max-width: 2700px;
    margin-inline: auto;
}

@media(min-width:1024px) {
    .leftminus120 {
        margin-left: var(--leftminus120);
    }

    .rightminus120 {
        margin-right: var(--rightminus120);
    }
}

@media (min-width:1500px) {
    .leftminus120 {
        margin-left: -12rem;
    }

    .rightminus120 {
        margin-right: -12rem;
    }
}

.anchorFocusVisible {
    outline: none;
}

.links {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    font-size: 0;
}

.customList {
    padding-left: 0;
    list-style: none;
    margin-bottom: 0;
}

.customList li {
    position: relative;
    font: var(--font-16);
    padding-left: 2rem;
    color: var(--color-21);
}

.customList li::before {
    content: '';
    position: absolute;
    width: 0.6rem;
    height: 0.6rem;
    background-color: var(--color-73);
    border-radius: 100%;
    left: 0;
    top: 0.8rem;
}

/* Accordion Area Start */
.accordionButton {
    position: relative;
    cursor: pointer;
    display: flex;
    gap: 1rem;
    justify-content: space-between;
    align-items: center;
    background-color: transparent;
    width: 100%;
    text-align: left;
    font: var(--font-30);
    color: var(--color-2b);
    padding: 0;
    padding-block: 3rem;
    border: 0;
    transition: all 0.5s ease;
    -webkit-transition: all 0.5s ease;
    -moz-transition: all 0.5s ease;
    -ms-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
}

.accordionButton::after {
    content: '';
    position: relative;
    display: block;
    width: 1rem;
    height: 1rem;
    transition: all 0.5s ease;
    -webkit-transition: all 0.5s ease;
    -moz-transition: all 0.5s ease;
    -ms-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
}

.accordionButton.active::after {
    rotate: 180deg;
}

.accordionButton.active {
    background-color: var(--color-f6);
}

.accordionItem:nth-child(1) .accordionButton {
    border-top: 0;
}

.accordionCollapse {
    display: none;
}

.accordionItem:not(:first-child) {
    border-top: 0.1rem solid rgb(112 112 112 / 30%);
}

.accordionBody {
    background-color: var(--color-f6);
    padding-bottom: 4.9rem;
}

.tableResponsive {
    width: 100%;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
}

/* Modal Css */
.modal {
    display: none;
    position: fixed;
    z-index: 1090;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgb(0 0 0 / 70%);
    backdrop-filter: blur(1px);
    align-items: center;
    justify-content: center;
    opacity: 0;
    visibility: hidden;
    transition: display 0.3 sease, opacity 0.3s ease, visibility 0.3s ease;
}

.modal.show {
    opacity: 1;
    visibility: visible;
    display: flex;
}

.innerMainHasModalShow {
    z-index: 1090;
}

.bodyHasModalShow .innerpageBanner {
    z-index: 0;
}

.bodyHasModalShow {
    overflow: hidden;
}

.modalContent {
    background: var(--color-ff);
    max-width: 70rem;
    width: 100%;
    position: relative;
    animation: ModalfadeIn 0.3s ease-in-out;
    display: flex;
    flex-direction: column;
    max-height: calc(100% - 3rem* 2);
}

.closeModal {
    position: absolute;
    top: 0.5rem;
    right: 0.5rem;
    height: 3rem;
    width: 3rem;
    font-size: var(--font-21);
    line-height: normal;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--color-17);
    color: var(--color-ff);
    opacity: 0.8;
}

.closeModal:hover {
    opacity: 1;
}

.modalTitle {
    padding: 2rem;
    border-bottom: 0.1rem solid rgb(37 51 111 / 10%);
    padding-right: 6rem;
}

.modalBody {
    position: relative;
    flex: 1 1 auto;
    padding: 2rem;
    overflow-y: auto;
}

@keyframes ModalfadeIn {
    from {
        opacity: 0;
        transform: scale(0.9);
    }

    to {
        opacity: 1;
        transform: scale(1);
    }
}

/* Reveal Animations */
@media(min-width:992px) {
    .image,
    .image2 {
        opacity: 0;
    }

    .revealImage,
    .revealImage:after {
        position: absolute;
        animation-delay: 0s;
        animation-iteration-count: 1;
        animation-duration: 1600ms;
        animation-fill-mode: both;
        animation-timing-function: ease-in-out;
    }

    .revealImage2,
    .revealImage2:after {
        position: absolute;
        animation-delay: .1s;
        animation-iteration-count: 1;
        animation-duration: 1800ms;
        animation-fill-mode: both;
        animation-timing-function: ease-in-out;
    }

    .revealImage {
        opacity: 1;
        position: relative;
        animation-name: reveal-image;
    }

    .revealImage2 {
        opacity: 1;
        position: relative;
        animation-name: reveal-image2;
    }

    .revealImage3 {
        opacity: 1;
        position: relative;
        animation-name: fadeInline1;
        animation-delay: .1s;
        animation-iteration-count: 1;
        animation-duration: 1800ms;
        animation-fill-mode: both;
        animation-timing-function: ease-in-out;
        transform-origin: top;
    }

    @keyframes reveal-image {
        from {
            clip-path: inset(0 100% 0 0);
        }

        to {
            clip-path: inset(0 0 0 0);
        }
    }

    @keyframes reveal-image2 {
        from {
            clip-path: inset(0 0% 100% 0);
        }

        to {
            clip-path: inset(0 0 0 0);
        }
    }
}

.imageContainer {
    position: relative;
    overflow: hidden;
    padding-bottom: calc(50% / (var(--ratio)));
}

.customBullets {
    margin: 0;
    padding: 0;
}

.customBullets li {
    position: relative;
    list-style: none;
    font-size: var(--font-16);
    line-height: var(--line-height-24);
    color: var(--color-black);
    font-weight: normal;
    padding-left: 3.5rem;
    margin-bottom: 2.5rem;
}

.customBullets li:last-child {
    margin-bottom: 0;
}

.customBullets li::before {
    content: '';
    position: absolute;
    width: 0.5rem;
    height: 0.5rem;
    background-color: var(--color-a8);
    left: 0;
    top: 1rem;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}

@media(max-width:1549px) {
    .customBullets li {
        padding-left: 2rem;
    }

    .customBullets li::before {
        width: 0.56rem;
        height: 0.56rem;
        top: 0.6rem;
    }
}

@media(max-width:767px) {
    .customBullets li {
        padding-left: 2rem;
    }

    .customBullets li::before {
        width: 0.46rem;
        height: 0.46rem;
        top: 0.6rem;
    }
}

/* Search Area */
.searchSection {
    padding-block: 6rem 10rem;
    min-height: 60vh;
}

.searchBx {
    position: relative;
    background-color: var(--color-white);
    padding: 2rem 5rem 2rem 1.8rem;
    margin-bottom: 2rem;
}

.searchBx:last-child {
    margin-bottom: 0;
}

.searchBx h5 {
    font-size: var(--font-24);
    font-weight: 600;
    color: var(--color-black);
    line-height: 1.16;
    margin-bottom: 1.5rem;
    transition: all .5s ease-out;
}

.searchBx .link {
    font-size: var(--font-18);
    font-weight: 400;
    color: var(--color-black);
    line-height: 1.16;
    margin-bottom: 1.5rem;
    font-family: var(--font-intervogue);
}

.searchBx p {
    font-size: var(--font-18);
    font-weight: 400;
    color: var(--color-black);
    line-height: 1.4;
    margin-bottom: 0;
    font-family: var(--font-intervogue);
}

.searchBxHasAnchorHover h6 {
    color: var(--color-96);
}

.searchBxHasAnchorHover::after {
    background-position: left bottom;
    color: var(--color-white);
}

.searchBxHasAnchorHover::before,
.searchBx::before {
    background-color: var(--color-f0);
    position: absolute;
    bottom: 0;
    left: 0;
    content: '';
    height: 0.1rem;
    width: 60%;
    transition: all 0.5s ease;
}

.searchBx::after {
    content: '\F285';
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 2rem;
    font-family: "Bootstrap Icons", sans-serif !important;
    flex-shrink: 0;
    width: 2.6rem;
    height: 2.6rem;
    line-height: 1;
    font-size: var(--font-11);
    border: 0.1rem solid #D2D2D2;
    color: var(--color-white);
    background-color: transparent;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    font-weight: 800;
    background: linear-gradient(to right, var(--color-84) 49%, var(--color-96) 49%);
    background-size: 210% 110%;
    background-position: right bottom;
    transition: all 0.5s ease-out;
}

.strechedLink {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 99;
    font-size: 0;
}

.searchBx:hover::before {
    background-color: var(--color-f0);
    width: 100%;
}